/* STATIC STYLES (desktop-first) */

.portfolio {
  position: relative;

  display: flex;
  flex-direction: column;

  width: 100vw;

  gap: 4rem;
  padding-inline: 3rem;
}

.portfolio-card {
  display: flex;
  flex-direction: column;
  align-items: center;

  text-align: center;

  gap: 1rem;
}

.portfolio-card h2 {
  white-space: nowrap;
}

.earth-container {
  width: 100%;
  height: 30rem;

  overflow: visible;
}

