/* STATIC STYLES (desktop-first) */

.timeline-container {
  position: relative;

  width: 100%;
  margin: 0 auto;
  padding: 2rem 0 0 0;
}

.timeline-container::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;

  width: 4px;
  margin-left: -2px;

  background: var(--color-primary-text-dark);
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .timeline-container::before {
    left: 2rem;
  }
}

@media (max-width: 576px) {
  .timeline-container::before {
    left: 0;
  }
}
