/* STATIC STYLES (desktop-first) */

.timeline-element {
  position: relative;

  width: 100%;
  margin-bottom: 3rem;

  opacity: 0;

  transform: translateX(0);
}

.timeline-element.visible:nth-child(odd) {
  animation: slideInOdd 0.6s ease-out forwards;
  animation-delay: 0.1s;
}
.timeline-element.visible:nth-child(even) {
  animation: slideInEven 0.6s ease-out forwards;
  animation-delay: 0.1s;
}

.timeline-icon {
  position: absolute;
  left: 50%;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 60px;
  height: 60px;

  border: 2px solid var(--color-primary-text-dark);
  border-radius: 50%;

  transform: translateX(-50%);
}

.timeline-element.visible:nth-child(odd) .timeline-icon, .timeline-element.visible:nth-child(even) .timeline-icon {
  animation: borderPulse 0.6s ease-out forwards;
  animation-delay: 0.2s;
}

.icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon-wrapper img {
  width: 2rem;
}

.timeline-description {
  position: absolute;
  top: 0;

  opacity: 0;

  white-space: nowrap;
  color: var(--color-primary-text-dark);

  transform: translateX(0);
}

.timeline-element:nth-child(odd) .timeline-description {
  left: calc(50% + 40px);

  text-align: start;
}
.timeline-element:nth-child(even) .timeline-description {
  right: calc(50% + 40px);

  text-align: end;
}

.timeline-element.visible:nth-child(odd) .timeline-description {
  animation: descSlideInOdd 0.6s ease-out forwards;
  animation-delay: 0.15s;
}
.timeline-element.visible:nth-child(even) .timeline-description {
  animation: descSlideInEven 0.6s ease-out forwards;
  animation-delay: 0.15s;
}

.timeline-content {
  position: relative;

  width: 45%;
  padding: 2rem 1rem;

  border-radius: 1rem;
}

.timeline-element:nth-child(odd) .timeline-content {
  left: -10px;
}
.timeline-element:nth-child(even) .timeline-content {
  left: calc(55% + 10px);
}

.content-arrow {
  position: absolute;
  top: 20px;

  width: 0;
  height: 0;

  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}

.timeline-element:nth-child(odd) .content-arrow {
  right: -10px;

  border-left: 10px solid var(--color-secondary-dark);
}
.timeline-element:nth-child(even) .content-arrow {
  left: -10px;

  border-right: 10px solid var(--color-secondary-dark);
}

@keyframes slideInOdd {
  0% {
    opacity: 0;

    transform: translateX(-100px);
  }
  60% {
    opacity: 1;

    transform: translateX(50px);
  }
  100% {
    opacity: 1;

    transform: translateX(0);
  }
}
@keyframes slideInEven {
  0% {
    opacity: 0;

    transform: translateX(100px);
  }
  60% {
    opacity: 1;

    transform: translateX(-50px);
  }
  100% {
    opacity: 1;

    transform: translateX(0);
  }
}
@keyframes borderPulse {
  0% {
    border-width: 2px;
  }
  50% {
    border-width: 7px;
  }
  100% {
    border-width: 2px;
  }
}
@keyframes descSlideInOdd {
  0% {
    opacity: 0;

    transform: translateX(50px);
  }
  60% {
    opacity: 1;

    transform: translateX(-20px);
  }
  100% {
    opacity: 1;

    transform: translateX(0);
  }
}
@keyframes descSlideInEven {
  0% {
    opacity: 0;

    transform: translateX(-50px);
  }
  60% {
    opacity: 1;

    transform: translateX(20px);
  }
  100% {
    opacity: 1;

    transform: translateX(0);
  }
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .timeline-element {
    padding-top: 3rem;
    padding-left: 6rem;
  }

  .timeline-element.visible:nth-child(odd) {
    animation: slideInEven 0.6s ease-out forwards;
    animation-delay: 0.1s;
  }
  .timeline-element.visible:nth-child(even) {
    animation: slideInEven 0.6s ease-out forwards;
    animation-delay: 0.1s;
  }

  .timeline-icon {
    left: 2rem;
  }

  .timeline-element:nth-child(odd) .timeline-description, .timeline-element:nth-child(even) .timeline-description {
    left: 6rem;

    text-align: left;
  }

  .timeline-element.visible:nth-child(odd) .timeline-description {
    animation: descSlideInEven 0.6s ease-out forwards;
    animation-delay: 0.15s;
  }
  .timeline-element.visible:nth-child(even) .timeline-description {
    animation: descSlideInEven 0.6s ease-out forwards;
    animation-delay: 0.15s;
  }

  .timeline-content {
    left: 0 !important;

    width: 90%;
  }

  .content-arrow {
    top: 10px;
  }

  .timeline-element:nth-child(odd) .content-arrow, .timeline-element:nth-child(even) .content-arrow {
    right: auto;
    left: -10px;

    border-right: 10px solid var(--color-secondary-dark);
    border-left: none;
  }
}

@media (max-width: 576px) {
  .timeline-element {
    padding-left: 3rem;
  }

  .timeline-icon {
    left: 0;

    width: 40px;
    height: 40px;
  }


  .timeline-element:nth-child(odd) .timeline-description, .timeline-element:nth-child(even) .timeline-description {
    left: 3rem;
  }
}
